import React, { Component } from 'react';
import { Card, Row, Col, Spin, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnLock, BtnExport, BtnExportPdf } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import {
  getStudentInTORForGrading,
  updateGradeByTOR,
  setLockedByTORId,
  exportExcel,
  exportPDF,
} from 'services/testOrganizing';
import { getFormTableData, convertTableDataObjToArray } from 'utils/formUtil';

import CreditClassTable from './CreditClassTable';
import CreditClass from './CreditClass';

class InsertScorePage extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.state = {
      loading: false,
      reload: 0,
      dataScore: null,
      rowKey: 'studentId',
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getTableData = values => {
    const formDataObj = getFormTableData(values);
    const formDataArray = convertTableDataObjToArray(formDataObj);
    return formDataArray;
  };

  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.yearEduId &&
        !!paramSearch.semesterId &&
        !!paramSearch.trainTypeId &&
        !!paramSearch.facultyId &&
        !!paramSearch.subjectId &&
        !!paramSearch.examBatch &&
        !!paramSearch.torId
      ) {
        this.setState({
          loading: true,
        });
        const response = await getStudentInTORForGrading(paramSearch);
        const tempData = response.data.items.map(item => {
          if (item.gradeParts && item.gradeParts.length) {
            item.gradeParts.forEach(gradePart => {
              item[gradePart.gradePartId] = gradePart.grade;
            });
          }
          return item;
        });

        this.setState({
          dataScore: [...tempData],
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleSave = async () => {
    try {
      const { dataScore } = this.state;
      if (dataScore && dataScore.length) {
        this.setState({
          loading: true,
        });
        this.refForm.current.validateFields(async (err, values) => {
          if (!err) {
            let formDataArray = this.getTableData(values);
            formDataArray = formDataArray.map((item, index) => {
              item.torId = dataScore[index].torId;
              item.studentId = dataScore[index].studentId;
              item.grade = parseFloat(item.grade);
              return item;
            });
            await updateGradeByTOR({
              torId: formDataArray[0].torId,
              details: [...formDataArray],
            });
            this.setState(
              {
                dataScore: null,
                loading: false,
              },
              () => this.handleChangeQueryParams()
            );
            message.success('Lu thnh cng');
          }
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handeLock = async () => {
    try {
      const { dataScore } = this.state;
      if (dataScore && dataScore.length) {
        this.setState({
          loading: true,
        });
        await setLockedByTORId({
          torId: dataScore[0].torId,
          isLocked: !dataScore[0].isLocked,
        });
        message.success(`${dataScore[0].isLocked ? 'M kha' : 'Kha'} thnh cng`);
        this.setState(
          {
            dataScore: null,
            loading: false,
          },
          () => this.handleChangeQueryParams()
        );
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleExportPDF = async () => {
    const paramSearch = getQueryParams();
    try {
      if (paramSearch.torId) {
        this.setState({
          loading: true,
        });
        await exportPDF(paramSearch.torId);
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleExportExcel = async () => {
    const paramSearch = getQueryParams();
    try {
      if (paramSearch.torId) {
        this.setState({
          loading: true,
        });
        await exportExcel(paramSearch.torId);
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  render() {
    const { dataScore, reload, rowKey, loading } = this.state;
    const isDisable = !(dataScore && dataScore.length);
    return (
      <Spin spinning={loading}>
        <Card title="Nhp im">
          <Row>
            <Col span={24}>
              <CreditClass />
            </Col>
          </Row>
          <Row>
            <Col span={24} style={{ textAlign: 'right' }}>
              <div className="btn-group">
                <BtnSave onClick={() => this.handleSave()} disabled={isDisable} />
                <BtnLock
                  title={
                    dataScore && dataScore.length && dataScore[0].isLocked ? 'M kha' : 'Kha'
                  }
                  onClick={() => this.handeLock()}
                  disabled={isDisable}
                />
                <BtnExportPdf onClick={() => this.handleExportPDF()} disabled={isDisable} />
                <BtnExport onClick={() => this.handleExportExcel()} disabled={isDisable} />
              </div>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <CreditClassTable
                reload={reload}
                data={dataScore}
                ref={this.refForm}
                rowKey={rowKey}
              />
            </Col>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <InsertScorePage />
  </Page>
);



